import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {EncodingComponent} from './encoding.component';

const routes: Routes = [
  {path: '', component: EncodingComponent},
  {path: 'universal', loadChildren: () => import('./universal/universal.module').then(m => m.UniversalModule)},
  {path: 'base64', loadChildren: () => import('./base64/base64.module').then(m => m.Base64Module)}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class EncodingRoutingModule {
}
